
.detailBoxWrapper{
    padding: 10px;
    display: flex;
    width: 100vw;
    height: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid red;
}


.itemContainer li{
    list-style: none;
    list-style-type: none;
    padding: 3px;
    border-bottom: 2px dotted red;

}
.itemContainer i{
    color: blue;
    font-size: larger;
}