

* {box-sizing: border-box}

.formWrapper{
    width: 100vw;
    height: 60vh;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(199, 224, 247);
}


input{
    width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    display: inline-block;
    border: none;  width: 100%;
    padding: 15px;
    margin: 5px 0 22px 0;
    display: inline-block;
    border: none;
    background: #f1f1f1;
}

input:focus{
    border: 1px solid green;
    outline: none;
}

.submitBtn{
    width: 70px;
    height: 40px;
    border-radius: 20px;
    outline: none;
    border: none;
}

.submitBtn:hover{
    border: 1px solid green;
    color: green;
}